import { Component, OnDestroy, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Router, ActivatedRoute } from '@angular/router';

import {
  CreateLoginInputDto,
  ERROR_MESSAGE,
  LoginService,
  OtpService,
  CreateRequestOtpDto,
  ProfileService
} from '@youtap-web-lib';
import { ModalService } from '../../../shared/service/modal/modal.service';
import { OtpModalComponent } from '../otp-modal/otp-modal.component';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AuthData } from '../../../shared/data/auth.data';
import { AppData } from '../../../shared/data/app-data';
import { WindowActionService } from '../../../shared/service/window-action.service';
import { GaService } from '../../../shared/service/ga.service';
import { AddressResolver } from '../../../shared/resolvers/address.resolver';
import { ProfileResolver } from '../../../shared/resolvers/profile.resolver';

@Component({
  selector: 'login',
  templateUrl: './otp-login.component.html'
})
export class OtpLoginComponent implements OnInit, OnDestroy {
  inputValue = '';
  countDown = 180;
  loadingOtp = false;
  errorInvalidOtp = false;
  errorMaximumAttempt = false;
  invalidCount = 0;
  error = false;
  msisdn!: string;
  errorTooMuchRequest = false;
  errorExpiredOtp = false;
  loginForm!: FormGroup;
  errorMessage = '';
  pin = '';
  checkData = 0;

  constructor(
    private location: Location,
    private router: Router,
    private route: ActivatedRoute,
    private loginService: LoginService,
    private otpService: OtpService,
    private modalService: ModalService,
    private formBuilder: FormBuilder,
    private authData: AuthData,
    public appData: AppData,
    private windowActionService: WindowActionService,
    private gaService: GaService,
    private activatedRoute: ActivatedRoute,
    private profileService: ProfileService,
    private addressResolver: AddressResolver,
    private profileResolver: ProfileResolver
  ) {
    this.loginForm = this.formBuilder.group({
      pin: [
        '',
        Validators.compose([Validators.required, Validators.maxLength(20)])
      ]
    });
  }

  ngOnInit(): void {
    this.msisdn = this.route.snapshot.queryParamMap.get('msisdn') ?? '';
    setInterval(() => {
      if (this.countDown > 0) {
        this.countDown--;
        if (this.countDown === 0) {
          this.gaService.addEvent('otp timeout', 'login', '');
          this.errorInvalidOtp = true;
          this.error = true;
        }
      }
    }, 1000);
  }

  goBack() {
    this.location.back();
  }

  goToOTP() {
    this.router.navigate(['/'], {});
  }

  convertToZero(value: string) {
    if (value[0] === '0') {
      return '62' + this.removeNonNumericCharacters(value.slice(1));
    } else {
      return this.removeNonNumericCharacters(value);
    }
  }

  onPinChanged(pin: string) {
    this.error = false;
    this.errorInvalidOtp = false;
    this.errorMaximumAttempt = false;
    this.errorExpiredOtp = false;
    this.errorMessage = '';
    this.pin = pin;
    this.loadingOtp = true;
    const modalRef = this.modalService.open(OtpModalComponent, {
      title: '',
      closeButton: false,
      data: {
        title: 'Sedang memverifikasi OTP',
        image: './assets/images/png/verify-otp.png'
      }
    });

    const data: CreateLoginInputDto = {
      loginInput: {
        mid: this.convertToZero(this.msisdn),
        otp: pin
      }
    };
    this.loginService.createLoginMutation(data).subscribe({
      next: ({ data }) => {
        this.loadingOtp = false;
        if (data) {
          this.gaService.addEvent('otp success', 'login', '');
          this.authData.token = data.login.authToken.accessToken;
          this.authData.expiredDate = data.login.authToken.expiredAt;
          this.addressResolver.resolve();
          this.profileResolver.resolve();
          this.checkData = setInterval((a) => {
            if (this.authData.user) {
              localStorage.removeItem('dateBanner');
              const stateUrl =
                this.activatedRoute.snapshot.queryParams['stateUrl'];
              modalRef.close('');
              if (stateUrl) {
                this.router.navigate([stateUrl]);
              } else {
                this.location.historyGo(-2);
              }
              clearInterval(a);
            }
          }, 2000);
        }
      },
      error: (error) => {
        modalRef.close('');
        if (error?.message?.toLowerCase() === ERROR_MESSAGE.INVALID_OTP) {
          this.errorExpiredOtp = false;
          this.errorInvalidOtp = true;
          this.error = true;
          this.invalidCount = this.invalidCount + 1;
          this.gaService.addEvent(
            'otp invalid',
            'login',
            ERROR_MESSAGE.INVALID_OTP
          );
        }
        if (error?.message?.toLowerCase() === ERROR_MESSAGE.EXPIRED_OTP) {
          this.errorInvalidOtp = false;
          this.error = true;
          this.errorExpiredOtp = true;
          this.invalidCount = this.invalidCount + 1;
          this.gaService.addEvent(
            'otp timeout',
            'login',
            ERROR_MESSAGE.EXPIRED_OTP
          );
        }

        if (error?.message?.toLowerCase().includes('16 unauthenticated')) {
          this.errorExpiredOtp = false;
          this.errorInvalidOtp = false;
          this.errorMaximumAttempt = true;
          this.error = true;
          if (error.message.includes('menit')) {
            this.errorMessage = error?.message.split(' ')[14].split('menit')[0];
          }
          this.gaService.addEvent(
            'otp maximum attempt',
            'login',
            ERROR_MESSAGE.MAXIMUM_3_ATTEMPT
          );
        }

        if (
          error?.message?.toLowerCase() ==
          ERROR_MESSAGE.UNREGISTERED_USER_APLICATION
        ) {
          this.router.navigate(['/register']);
        }
        this.loadingOtp = false;
        this.error = true;
      }
    });
  }
  handleError(event: string) {
    if (event) {
      this.errorExpiredOtp = false;
      this.errorInvalidOtp = false;
      this.errorMaximumAttempt = false;
      this.errorMessage = '';
      this.errorTooMuchRequest = false;
    }
  }

  submitPhoneNumber() {
    this.loadingOtp = true;
    const modalRef = this.modalService.open(OtpModalComponent, {
      title: '',
      closeButton: false,
      data: {
        title: 'Sedang mengirim OTP',
        image: './assets/images/png/sending-otp.png'
      }
    });

    const data: CreateRequestOtpDto = {
      requestOtpInput: {
        msisdn: this.convertToZero(this.msisdn),
        type: 'OTP_TYPE_LOGIN'
      }
    };

    this.otpService.requestOtpMutation(data).subscribe({
      next: (data) => {
        this.loadingOtp = false;
        modalRef.close('');
        if (data) {
          this.countDown = 180;
        }
      },
      error: (error) => {
        modalRef.close('');
        if (error?.message.includes(ERROR_MESSAGE.TOO_MUCH_REQUEST_OTP)) {
          this.errorTooMuchRequest = true;
          if (error.message.includes('m')) {
            this.errorMessage = error?.message.split(' ')[7].split('m')[0];
          } else if (error.message.includes('.')) {
            this.errorMessage = error?.message.split(' ')[7].split('.')[0];
          }
        }
        this.loadingOtp = false;
        this.error = true;
      }
    });
  }

  removeNonNumericCharacters(input: string) {
    return input.replace(/\D/g, '');
  }

  ngOnDestroy() {
    clearInterval(this.checkData);
  }
}
