import {Component, OnInit} from '@angular/core';
import {Location} from '@angular/common';
import {ActivatedRoute, Router} from '@angular/router';
import {CreateRequestOtpDto, OtpService, ERROR_MESSAGE, ValidationService} from '@youtap-web-lib';
import {OtpModalComponent} from './otp-modal/otp-modal.component';
import {ModalService} from '../../shared/service/modal/modal.service';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {AppData} from '../../shared/data/app-data';
import {GtmService} from "../../shared/service/gtm.service";
import {GaService} from "../../shared/service/ga.service";
import {MetaService} from '../../shared/service/meta.service';

@Component({
  selector: 'login',
  templateUrl: './login.component.html'
})
export class LoginComponent implements OnInit {
  inputValue = '';
  loadingOtp = false;
  error = false;
  errorTooMuchRequest = false;
  errorUnregistered = false;
  loginForm!: FormGroup;
  errorMessage = '';

  constructor(
    private location: Location,
    private router: Router,
    private otpService: OtpService,
    private modalService: ModalService,
    private formBuilder: FormBuilder,
    public appData: AppData,
    private gtmService: GtmService,
    private gaService: GaService,
    private metaService: MetaService,
    private activatedRoute: ActivatedRoute
  ) {
    this.loginForm = this.formBuilder.group({
      msisdn: [
        '',
        Validators.compose([
          Validators.required,
          Validators.maxLength(20),
          Validators.minLength(8),
          ValidationService.numValidator
        ])
      ]
    });
  }

  src = '/libs/shared-assets/src/images/svg/graphic-login.svg';

  ngOnInit()
    :
    void {
    this.metaService.setMeta({
      title:
        'Login - Youtap BOS',
      description:
        'Login akun usahamu di Youtap BOS dengan input nomor hp untuk menerima OTP untuk masuk ke dashboard akun usahamu.',
      image:
        'https://storage.googleapis.com/production-youtap-public/banner/IlbfMVveQ=s0.png'
    });
  }

  goBack() {
    this.router.navigate(['/']);
  }

  goToRegister() {
    this.gaService.addEvent('button register', 'login', '')
    this.router.navigate(['/register']);
  }

  onValueChange(value: string) {
    this.inputValue = value;
  }

  convertToZero(value: string) {
    if (value[0] === '0') {
      return '62' + value.slice(1);
    } else {
      return value;
    }
  }

  submitPhoneNumber() {
    this.loadingOtp = true;
    const modalRef = this.modalService.open(OtpModalComponent, {
      title: '',
      closeButton: false,
      data: {
        title: 'Sedang mengirim OTP',
        image: './assets/images/png/sending-otp.png'
      }
    });
    const msisdn = this.loginForm.controls['msisdn'].value;

    const data: CreateRequestOtpDto = {
      requestOtpInput: {
        msisdn: this.convertToZero(msisdn),
        type: 'OTP_TYPE_LOGIN'
      }
    };

    this.otpService.requestOtpMutation(data).subscribe({
      next: (data) => {
        this.loadingOtp = false;
        if (data) {
          modalRef.close('');
          this.router.navigate(['/login/login-otp',], {
            queryParams: {
              msisdn: this.loginForm.controls['msisdn'].value,
              stateUrl: this.activatedRoute.snapshot.queryParams['stateUrl']
            }
          });
        }
      },
      error: (error) => {
        modalRef.close('');
        if (error?.message?.toLowerCase() === ERROR_MESSAGE.UNREGISTERED_USER) {
          this.errorUnregistered = true;
          // this.gtmService.addEvent('login', 'invalid user')
          this.gaService.addEvent('invalid number', 'login', ERROR_MESSAGE.UNREGISTERED_USER)
        }
        if (error?.message.includes(ERROR_MESSAGE.TOO_MUCH_REQUEST_OTP)) {
          this.errorTooMuchRequest = true;
          if (error.message.includes('m')) {
            this.errorMessage = error?.message.split(' ')[7].split('m')[0] + ' menit';
          } else if (error.message.includes('.')) {
            this.errorMessage = error?.message.split(' ')[7].split('.')[0] + ' detik';
          }
        }
        this.loadingOtp = false;
        this.error = true;
      }
    });
  }

  refreshPage() {
    window.location.reload();
  }

  changeNumber() {
    this.errorUnregistered = false;
    this.errorTooMuchRequest = false;
  }
}
